.card {
  background-color: #a8ff35;
  border-radius: 12px;
  width: 30%;
  height: 21.2rem;
  position: relative;
  margin-top: 1rem;
  margin-left: 5rem;
}

.title {
  color: #a8ff35;
  text-align: center;
}
.subtitle {
  color: white;
  text-align: center;
  text-decoration: underline;
  font-size: medium;
  font-weight: 500;
}
.target {
  color: #a8ff35;
  text-align: center;
  font-size: 35px;
}
.target span {
  color: #a8ff35;
}

.message {
  color: #a8ff35;
  text-align: center;
  font-weight: 600;
  font-size: 20px;
}
.message span {
  color: white;
  font-size: 25px;
}
.title span {
  color: white;
  font-weight: bold;
  font-size: 30px;
}

.cardorg {
  background: #a8ff35;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}

button {
  margin: 5px 3px 5px 5px;
  margin-top: 10px;
  background-color: hsl(86, 92%, 61%);
  border-radius: 20px;
  padding: 20px;
  padding-top: 10px;
  color: #060602;
  font-size: large;
  height: 3rem;
  width: 5rem;
  border-color: #a8ff35;
  display: flex;
  text-align: center;
  font-weight: bold;
  font-size: 26px;
}

button:hover {
  background-color: black;
  color: #a8ff35;
  width: 5.2rem;
  height: 3.1rem;
  font-size: 20px;
  transition: 0.2s;
}

.box {
  position: relative;
  background-color: #a8ff35;
  width: 10rem;
  height: 10rem;
  top: 20vh;
  border-radius: 15px;
  margin-left: 40%;
  text-align: center;
}
.scorecard {
  background-color: black;
  color: #a8ff35;
  border-color: #a8ff35;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  margin-top: 2px;
  margin-bottom: 20%;
}

.target {
  color: white;
}
.target div {
  color: #a8ff35;
  font-size: 100px;
}
.target .msg {
  color: #a8ff35;
  font-size: 30px;
}
.target span {
  font-weight: bold;
}

.box h1 {
  font-size: 40px;
}

