.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(42, 39, 39, 0.75);
}

.modal {
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
  animation: slide-down 300ms ease-out forwards;
}

.header {
  background: black;
  padding: 1rem;
}

.header p {
  color: white;
  font-style: italic;
}

.header h2 {
  margin: 0;
  color: #a8ff35;
}

.content {
  padding: 1rem;
  color: black;
  font-size: 20px;
  font-weight: 600;
  /* font-style: italic; */
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-start;
}
.actions button {
  font-size: medium;
  background-color: black;
  width: 125px;
  text-align: center;
  color: #a8ff35;
}
.actions .btn2 {
  width: 110px;
  color: black;
  background-color: #a8ff35;
  border-color: black;
  text-align: center;
  align-items: center;
  padding: 0.5rem 2rem;
}
.actions .btn2:hover {
  background-color: black;
  color: #a8ff35;
  border-color: black;
}

.actions button:hover {
  background-color: #a8ff35;
  color: black;
  border-color: black;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}
@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
