.close {
  height: 100vh;
  width: 100%;
  background-color: black;
}
.close span {
  color: white;
}
.close h1 {
  text-align: center;
  color: #a8ff35;
  padding-top: 20%;
}

.scorecard {
  display: flex;
  flex-wrap: wrap;
  background-color: black;
  width: 500px;
  height: 300px;
  margin-left: 35%;
  margin-top: 5%;
  border-radius: 14px;
}
.scorecard div {
  color: black;
  padding-left: 27%;
  padding-top: 15%;
  font-size: 100px;
  background-color: #a8ff35;
  border-radius: 25px;
  width: 500px;
}

.card {
  display: flex;
  flex-direction: row;
  margin-left: 35%;
  margin-top: 2%;
  height: 50px;
  width: 500px;
}
.card button {
  color: black;
  background-color: #a8ff35;
  font-size: large;
  font-weight: 800;
  font-size: 22px;
  width: 100px;
  text-align: center;
  border-radius: 25px;
}
.card button:hover {
  background-color: black;
  color: #a8ff35;
  border: 0.3px solid;
  border-color: #a8ff35;
  font-size: 15px;
  width: 110px;
}
@media (max-width: 768px) {
  .scorecard {
    margin-left: 0%;
  }
  .card {
    margin-left: 0%;
  }
}
